'use strict'

export default function (router) {
  router.map({
    '*': {
      component: function (resolve) {
        require(['./views/fangbian.vue'], resolve)
      }
    },
    '/': {
      name: 'home',
      component: function (resolve) {
        require(['./views/fangbian.vue'], resolve)
      }
    },
    '/fangbian': {
      name: 'fangbian',
      component: function (resolve) {
        require(['./views/fangbian.vue'], resolve)
      }
    },
    '/xingcheng': {
      name: 'xingcheng',
      component: function (resolve) {
        require(['./views/xingcheng.vue'], resolve)
      }
    },
    '/haochi': {
      name: 'haochi',
      component: function (resolve) {
        require(['./views/haochi.vue'], resolve)
      }
    },
    '/haowan': {
      name: 'haowan',
      component: function (resolve) {
        require(['./views/haowan.vue'], resolve)
      }
    },
    '/ketao': {
      name: 'ketao',
      component: function (resolve) {
        require(['./views/ketao.vue'], resolve)
      }
    }
  })
}



